.carousel-wrapper {
    position: relative;
    width: 990px;
    margin: auto;
    height: 400px;
    overflow: hidden;
    --border-radius: 20px;
}

.carousel-wrapper:hover .next {
    right: 0;
    opacity: 1;

}

.carousel-wrapper:hover .prev {
    left: 0;
    opacity: 1;
}

.carousel {
    margin: 0;
    padding: 0;
    /* width: 3960px; */
    position: absolute;

}

.carousel li {
    list-style: none;
    float: left;
    display: none;
    transition: all ease 0.9s;

}
.carousel li.display-block {
    display: block;
}

.carousel li a {
    color: inherit;
}

.carousel-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 12px 10px;
    border: none;
    color: #fff;
    font-size: 20px;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.3);
    cursor: pointer;
    transition: left ease 0.3s, right ease 0.3s, opacity ease 0.3s;

}

.carousel-btn.next {
    right: -35px;
}

.carousel-btn.prev {
    left: -35px;
}

.dot-wrapper {

    width: auto;
    padding: 5px 10px;
    position: absolute;
    left: 50%;
    bottom: 10px;
    transform: translateX(-50%);

}

.dot-wrapper>span {
    width: 10px;
    height: 10px;
    display: inline-block;
    background-color: #6A6B6B;
    border-radius: 50%;
    border: 2px solid #6A6B6B;
    cursor: pointer;
    transition: all ease 0.3s;
}

.dot-wrapper>span:not(:last-child) {
    margin-right: 10px;
}
.dot-wrapper>span:hover,
.dot-wrapper>span.active {
    background-color: #fff;
    border: 2px solid rgba(255, 5, 5, 0.6);
}

/* Fading animation */
.fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
}

@-webkit-keyframes fade {
    from {
        opacity: .4
    }

    to {
        opacity: 1
    }
}

@keyframes fade {
    from {
        opacity: .4
    }

    to {
        opacity: 1
    }
}