.carousel-wrapper-slide {
    position: relative;
    width: 990px;
    margin: auto;
    height: 400px;
    overflow: hidden;
    --border-radius: 20px;
}

.carousel-wrapper-slide:hover .next {
    right: 0;
    opacity: 1;

}

.carousel-wrapper-slide:hover .prev {
    left: 0;
    opacity: 1;
}

.carousel-slide {
    margin: 0;
    padding: 0;    
    position: absolute;
    /* width: 4950px; */

}

.carousel-slide li {
    list-style: none;
    float: left;
    /* display: none; */
    /* transition: all ease 0.9s; */

}

.carousel-slide li a {
    color: inherit;
}

.carousel-btn-slide {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 12px 10px;
    border: none;
    color: #fff;
    font-size: 20px;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.3);
    cursor: pointer;
    transition: left ease 0.3s, right ease 0.3s, opacity ease 0.3s;

}

.carousel-btn-slide.next {
    right: -35px;
}

.carousel-btn-slide.prev {
    left: -35px;
}

.dot-wrapper-slide {
    width: auto;
    position: absolute;
    left: 50%;
    bottom: 10px;
    transform: translateX(-50%);

}

.dot-wrapper-slide > span {
    width: 10px;
    height: 10px;
    display: inline-block;
    background-color: #6A6B6B;
    border-radius: 50%;
    border: 2px solid #6A6B6B;
    cursor: pointer;
    transition: all ease 0.3s;
}

.dot-wrapper-slide > span:not(:last-child) {
    margin-right: 10px;
}
.dot-wrapper-slide>span:hover,
.dot-wrapper-slide>span.active {
    background-color: #fff;
    border: 2px solid rgba(255, 5, 5, 0.6);
}